body{
    margin: 0;
    background-color: #333333;
}

h1{
    color:white;
    text-align: center;
    font-family: monospace;
}

.container{
    display: grid;
    grid-template-columns: 1fr 100px 1fr;
    align-items: center;
    padding: 70px;
    padding-top: 15px;
    gap: 40px;
    height: 80vh;
}

.input-area{
    height: 100%;
    padding: 20px;
    border-radius: 3%;
    box-sizing: border-box;
    color: #aaaaaa;
    background: #444444;
    border: none;
    transition: background 0.25s, color 0.25s;
}

.input-area:hover,
.input-area:focus{
    background: #4a4a4a;
}

.input-area:focus{
    color: #eeeeee;
}

.output-area{
    height: 100%;
    padding: 20px;
    border-radius: 3%;
    box-sizing: border-box;
    color: #aaaaaa;
    background: #444444;
    border: none;
    transition: background 0.25s, color 0.25s;
}

.output-area:hover,
.output-area:focus{
    background: #4a4a4a;
}

.output-area:focus{
    color: #eeeeee;
    outline: #eeeeee;
}



.format-btn{
    padding: 8px 14px;
    width: 100%;
    border-radius: 5px;
    border: none;
    margin-bottom: 40px;
}

.clear-btn{
    padding: 8px 14px;
    width: 100%;
    border-radius: 5px;
    border: none;
}